import { Component, OnInit } from '@angular/core';
import { AdminIndicadoresService } from '../../admin-indicadores.service';

@Component({
  selector: 'app-importar-bots',
  templateUrl: './importar-bots.component.html',
  styleUrls: ['./importar-bots.component.css']
})
export class ImportarBotsComponent implements OnInit {

  constructor(private adminindic: AdminIndicadoresService) {
    this.bots =[
      { label: "Inserir Orçados", value: 0 , funcao: "Carregar volume produzido", status: "S/I"},
      // { label: "Inserir Orçados", value: 1 , funcao: "Carregar volume produzido", status: "Sem Info"}
    ] 
    
  }

  bots: any[]=[]
  botsLista: any[]=[]
  responsiveOptions;

  ngOnInit() {
    
    this.botsLista = this.bots
    console.log(this.botsLista)
    this.StatusBot();
  }
 

  StatusBot(){
    for(var i =0; i<this.botsLista.length;i++){
      this.adminindic.statusBots(i).subscribe(
        res => { 
          var dado = JSON.parse(res).status;
          console.log(dado)
          dado = dado ==='Falha' ? 'S/I' : dado === 'Executando' ? 'Ocupado' : 'Pronto';
          this.botsLista[i]['status'] = dado;
        }
      );
    }
  }
  
  StartBot(bot){
    this.botsLista = []
    this.adminindic.startBot(bot).subscribe();
    this.StatusBot();
    this.botsLista = this.bots
  }
}
